import { SERVER_URL } from "../../config";
import axios from "axios";
const url = SERVER_URL + "/computers";
export default {
  namespaced: true,
  state: {
    list: [],
    updateComputer: null,
  },
  mutations: {
    setList(state, value) {
      state.list = value;
    },
    addNewComputer(state, newComputer) {
      state.list.push(newComputer);
    },
    setUpdateComputer(state, value) {
      state.updateComputer = value;
    },
    updateComputer(state, updateComputer) {
      const updateList = state.list.map((computer) => {
        if (computer._id == updateComputer._id) {
          return updateComputer;
        }
        return computer;
      });
      state.list = updateList;
    },
    deleteComputer(state, delComputerId) {
      const updateList = state.list.filter(
        (computer) => computer._id != delComputerId
      );
      state.list = updateList;
    },
  },
  actions: {
    create({ commit }, payload) {
      const { computer, notify } = payload;
      axios.post(url, computer).then((res) => {
        const result = parseInt(res.data.result);
        if (result > 0) {
          const newComputer = res.data.data;
          commit("addNewComputer", newComputer);
          notify({
            title: "Success",
            message: "New Computer is created.",
            type: "success",
          });
        } else {
          notify.error({
            title: "Failed to create computer",
            message: res.data.message,
          });
        }
      });
    },
    loadList({ commit }, payload) {
      const { notify } = payload;
      commit("setGlobalLoading", true, { root: 1 });
      axios
        .get(
          url +
            `/${
              localStorage.getItem("username") == "admin"
                ? "admin"
                : localStorage.getItem("companyId")
            }`
        )
        .then((res) => {
          commit("setGlobalLoading", false, { root: 1 });
          commit("setList", res.data);
        })
        .catch((err) => {
          commit("setGlobalLoading", false, { root: 1 });
          notify.error({
            title: "Failed to load computer",
            message: err.message,
          });
        });
    },
    update({ commit }, payload) {
      const { computer, notify } = payload;
      axios
        .put(url + "/" + computer._id, computer)
        .then(() => {
          commit("updateComputer", computer);
          notify({
            title: "Success",
            message: " Computer updated!",
            type: "success",
          });
        })
        .catch((err) => {
          notify.error({
            title: "Failed to update computer",
            message: err.message,
          });
        });
    },
    delete({ commit, state }, payload) {
      const { notify } = payload;
      const deleteComputerId = state.updateComputer._id;
      axios
        .delete(url + "/" + deleteComputerId)
        .then(() => {
          commit("deleteComputer", deleteComputerId);
          notify({
            title: "Success",
            message: "Computer deleted!",
            type: "success",
          });
        })
        .catch((err) => {
          notify.error({
            title: "Failed to delete computer",
            message: err.message,
          });
        });
    },
  },
};
