import { SERVER_URL } from "../../config";
import axios from "axios";
const url = SERVER_URL + "/users";
export default {
  namespaced: true,
  state: {
    list: [],
    updateUser: null,
  },
  mutations: {
    setList(state, value) {
      state.list = value;
    },
    addNewUser(state, newUser) {
      state.list.push(newUser);
    },
    setUpdateUser(state, value) {
      state.updateUser = value;
    },
    updateUser(state, updateUser) {
      const updateList = state.list.map((user) => {
        if (user._id == updateUser._id) {
          return updateUser;
        }
        return user;
      });
      state.list = updateList;
    },
    deleteUser(state, delUserId) {
      const updateList = state.list.filter((user) => user._id != delUserId);
      state.list = updateList;
    },
  },
  actions: {
    create({ commit, dispatch }, payload) {
      const { user, notify } = payload;
      commit("setGlobalLoading", true, { root: 1 });
      axios.post(url, user).then((res) => {
        commit("setGlobalLoading", false, { root: 1 });

        const result = parseInt(res.data.result);
        if (result > 0) {
          dispatch("loadList", { notify: notify });
          notify({
            title: "Success",
            message: "New User is created.",
            type: "success",
          });
        } else {
          notify.error({
            title: "Failed to create user",
            message: res.data.message,
          });
        }
      });
    },
    loadList({ commit }, payload) {
      const { notify } = payload;
      commit("setGlobalLoading", true, { root: 1 });
      axios
        .get(
          url +
            `/${
              localStorage.getItem("username") == "admin"
                ? "admin"
                : localStorage.getItem("companyId")
            }`
        )
        .then((res) => {
          commit("setGlobalLoading", false, { root: 1 });
          commit("setList", res.data);
        })
        .catch((err) => {
          commit("setGlobalLoading", false, { root: 1 });
          notify.error({
            title: "Failed to load user",
            message: err.message,
          });
        });
    },
    update({ dispatch }, payload) {
      const { user, notify } = payload;
      axios
        .put(url + "/" + user._id, user)
        .then(() => {
          dispatch("loadList", { notify: notify });
          notify({
            title: "Success",
            message: " User updated!",
            type: "success",
          });
        })
        .catch((err) => {
          notify.error({
            title: "Failed to update user",
            message: err.message,
          });
        });
    },
    delete({ commit, state }, payload) {
      const { notify } = payload;
      const deleteUserId = state.updateUser._id;
      commit("setGlobalLoading", true, { root: 1 });
      axios
        .delete(url + "/" + deleteUserId)
        .then(() => {
          commit("setGlobalLoading", false, { root: 1 });
          commit("deleteUser", deleteUserId);
          notify({
            title: "Success",
            message: "User deleted!",
            type: "success",
          });
        })
        .catch((err) => {
          commit("setGlobalLoading", false, { root: 1 });
          notify.error({
            title: "Failed to delete user",
            message: err.message,
          });
        });
    },
  },
};
