import Vue from "vue";
import Vuex from "vuex";

// Import modules
import user from "./modules/user";
import computer from "./modules/computer";
import message from "./modules/message";
import icon from "./modules/icon";
import group from "./modules/group";
import company from "./modules/company";

Vue.use(Vuex);

/* eslint-disable no-new */
const store = new Vuex.Store({
  state: {
    selectedSidebar: "Users",
    computerDialogVisibility: false,
    backgroundDialogVisibility: false,

    newUserDialogVisibility: false,
    editUserDialogVisibility: false,
    deleteUserDialogVisibility: false,

    newIconDialogVisibility: false,
    editIconDialogVisibility: false,
    deleteIconDialogVisibility: false,

    newComputerDialogVisibility: false,
    editComputerDialogVisibility: false,
    deleteComputerDialogVisibility: false,

    newMessageDialogVisibility: false,
    editMessageDialogVisibility: false,
    deleteMessageDialogVisibility: false,

    newCompanyDialogVisibility: false,
    editCompanyDialogVisibility: false,
    deleteCompanyDialogVisibility: false,

    globalLoading: false,
  },
  mutations: {
    setComputerDialogVisibility(state, value) {
      state.computerDialogVisibility = value;
    },
    setSelectedSidebar(state, value) {
      state.selectedSidebar = value;
    },
    setBackgroundDialogVisibility(state, value) {
      state.backgroundDialogVisibility = value;
    },
    setNewUserDialogVisibility(state, value) {
      state.newUserDialogVisibility = value;
    },
    setEditUserDialogVisibility(state, value) {
      state.editUserDialogVisibility = value;
    },
    setDeleteUserDialogVisibility(state, value) {
      state.deleteUserDialogVisibility = value;
    },
    setNewIconDialogVisibility(state, value) {
      state.newIconDialogVisibility = value;
    },
    setEditIconDialogVisibility(state, value) {
      state.editIconDialogVisibility = value;
    },
    setDeleteIconDialogVisibility(state, value) {
      state.deleteIconDialogVisibility = value;
    },
    setNewComputerDialogVisibility(state, value) {
      state.newComputerDialogVisibility = value;
    },
    setEditComputerDialogVisibility(state, value) {
      state.editComputerDialogVisibility = value;
    },
    setDeleteComputerDialogVisibility(state, value) {
      state.deleteComputerDialogVisibility = value;
    },
    setNewMessageDialogVisibility(state, value) {
      state.newMessageDialogVisibility = value;
    },
    setEditMessageDialogVisibility(state, value) {
      state.editMessageDialogVisibility = value;
    },
    setDeleteMessageDialogVisibility(state, value) {
      state.deleteMessageDialogVisibility = value;
    },
    setGlobalLoading(state, value) {
      state.globalLoading = value;
    },
    setNewCompanyDialogVisibility(state, value) {
      state.newCompanyDialogVisibility = value;
    },
    setEditCompanyDialogVisibility(state, value) {
      state.editCompanyDialogVisibility = value;
    },
    setDeleteCompanyDialogVisibility(state, value) {
      state.deleteCompanyDialogVisibility = value;
    },
  },
  modules: {
    user,
    computer,
    message,
    icon,
    group,
    company,
  },
});

export default store;
