import Vue from "vue";
import ElementUI from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import locale from "element-ui/lib/locale/lang/en";

import VueDraggable from "vue-draggable";
Vue.use(VueDraggable);

import VueDragDrop from "vue-drag-drop";
Vue.use(VueDragDrop);
Vue.config.productionTip = false;

Vue.use(ElementUI, { locale });

// filters
import filePath from "./filters/filePath";
Vue.filter("filePath", filePath);

import firebase from "firebase";
let app = "";
const config = {
  apiKey: "AIzaSyAJdiweJhLFX8547oXlMRwjw6_M74JMvLE",
  authDomain: "i3admin-302606.firebaseapp.com",
  projectId: "i3admin-302606",
  storageBucket: "i3admin-302606.appspot.com",
  messagingSenderId: "329386379565",
  appId: "1:329386379565:web:caa9842408f1efd3ea6ba4",
  measurementId: "G-N6410CMVMH",
};

firebase.initializeApp(config);

firebase.auth().onAuthStateChanged(() => {
  if (!app) {
    /* eslint-disable no-new */
    app = new Vue({
      router,
      store,
      render: (h) => h(App),
    }).$mount("#app");
  }
});

