import { SERVER_URL } from "../../config";
import axios from "axios";
const url = SERVER_URL + "/groups";
export default {
  namespaced: true,
  state: {
    list: [],
    updateGroup: null,
    activeComputers: [],
    backgroundImage: "",
  },
  mutations: {
    setList(state, value) {
      state.list = value;
    },
    addNewGroup(state, newGroup) {
      state.list.push(newGroup);
    },
    setUpdateGroup(state, value) {
      state.updateGroup = value;
    },
    updateGroup(state, updateGroup) {
      const updateList = state.list.map((group) => {
        if (group._id == updateGroup._id) {
          return updateGroup;
        }
        return group;
      });
      state.list = updateList;
    },
    deleteGroup(state, delGroupId) {
      const updateList = state.list.filter((group) => group._id != delGroupId);
      state.list = updateList;
    },
    setActiveComputers(state, activeComputers) {
      state.activeComputers = activeComputers;
    },
    setBackgroundImage(state, imagePath) {
      state.backgroundImage = imagePath;
    },
  },
  actions: {
    create({ commit, state }, payload) {
      const { group, notify } = payload;
      let newGroupObject = {
        name: group.name,
        activeIcons: group.activeIcons,
        computers: state.activeComputers,
        backgroundImage: state.backgroundImage,
        company: localStorage.getItem("companyId"),
      };
      axios.post(url, newGroupObject).then((res) => {
        const result = parseInt(res.data.result);
        if (result > 0) {
          const newGroup = res.data.data;
          commit("addNewGroup", newGroup);
          commit("setActiveComputers", []);
          notify({
            title: "Success",
            message: "New Group is created.",
            type: "success",
          });
        } else {
          notify.error({
            title: "Failed to create group",
            message: res.data.message,
          });
        }
      });
    },
    loadList({ commit }, payload) {
      const { notify } = payload;
      commit("setGlobalLoading", true, { root: 1 });
      axios
        .get(
          url +
            `/${
              localStorage.getItem("username") == "admin"
                ? "admin"
                : localStorage.getItem("companyId")
            }`
        )
        .then((res) => {
          commit("setGlobalLoading", false, { root: 1 });
          console.log(res.data);
          commit("setList", res.data);
        })
        .catch((err) => {
          commit("setGlobalLoading", false, { root: 1 });
          notify.error({
            title: "Failed to load group",
            message: err.message,
          });
        });
    },
    update({ dispatch, state }, payload) {
      const { group, notify } = payload;
      const updateGroupObject = {
        name: group.name,
        activeIcons: group.activeIcons,
        computers: state.activeComputers,
        backgroundImage: state.backgroundImage,
      };
      axios
        .put(url + "/" + group._id, updateGroupObject)
        .then(() => {
          dispatch("loadList", { notify: notify });
          notify({
            title: "Success",
            message: " Group updated!",
            type: "success",
          });
        })
        .catch((err) => {
          notify.error({
            title: "Failed to update group",
            message: err.message,
          });
        });
    },
    delete({ commit, state }, payload) {
      const { notify } = payload;
      const deleteGroupId = state.updateGroup._id;
      axios
        .delete(url + "/" + deleteGroupId)
        .then(() => {
          commit("deleteGroup", deleteGroupId);
          notify({
            title: "Success",
            message: "Group deleted!",
            type: "success",
          });
        })
        .catch((err) => {
          notify.error({
            title: "Failed to delete group",
            message: err.message,
          });
        });
    },
    getGroup({ commit }, payload) {
      const { groupId, notify } = payload;
      axios
        .get(url + "/" + groupId)
        .then((res) => {
          commit("setUpdateGroup", res.data);
          commit("setActiveComputers", res.data.computers);
          commit("setBackgroundImage", res.data.backgroundImage);
        })
        .catch((err) => {
          notify.error({
            title: "Failed to load group",
            message: err.message,
          });
        });
    },
  },
};
