import { SERVER_URL } from "../../config";
import axios from "axios";
const url = SERVER_URL + "/companies";
export default {
  namespaced: true,
  state: {
    list: [],
    updateCompany: null,
  },
  mutations: {
    setList(state, value) {
      state.list = value;
    },
    addNewCompany(state, newCompany) {
      state.list.push(newCompany);
    },
    setUpdateCompany(state, value) {
      state.updateCompany = value;
    },
    updateCompany(state, updateCompany) {
      const updateList = state.list.map((company) => {
        if (company._id == updateCompany._id) {
          return updateCompany;
        }
        return company;
      });
      state.list = updateList;
    },
    deleteCompany(state, delCompanyId) {
      const updateList = state.list.filter(
        (company) => company._id != delCompanyId
      );
      state.list = updateList;
    },
  },
  actions: {
    create({ commit }, payload) {
      const { company, notify } = payload;
      axios.post(url, company).then((res) => {
        const result = parseInt(res.data.result);
        if (result > 0) {
          const newCompany = res.data.data;
          commit("addNewCompany", newCompany);
          notify({
            title: "Success",
            message: "New Company is created.",
            type: "success",
          });
        } else {
          notify.error({
            title: "Failed to create company",
            message: res.data.message,
          });
        }
      });
    },
    loadList({ commit }, payload) {
      const { notify } = payload;
      axios
        .get(url)
        .then((res) => {
          commit("setList", res.data);
        })
        .catch((err) => {
          notify.error({
            title: "Failed to load company",
            message: err.message,
          });
        });
    },
    update({ commit }, payload) {
      const { company, notify } = payload;
      axios
        .put(url + "/" + company._id, company)
        .then(() => {
          commit("updateCompany", company);
          notify({
            title: "Success",
            message: " Company updated!",
            type: "success",
          });
        })
        .catch((err) => {
          notify.error({
            title: "Failed to update company",
            message: err.message,
          });
        });
    },
    delete({ commit, state }, payload) {
      const { notify } = payload;
      const deleteCompanyId = state.updateCompany._id;
      axios
        .delete(url + "/" + deleteCompanyId)
        .then(() => {
          commit("deleteCompany", deleteCompanyId);
          notify({
            title: "Success",
            message: "Company deleted!",
            type: "success",
          });
        })
        .catch((err) => {
          notify.error({
            title: "Failed to delete company",
            message: err.message,
          });
        });
    },
  },
};
