import { SERVER_URL } from "../../config";
import axios from "axios";
const url = SERVER_URL + "/icons";
export default {
  namespaced: true,
  state: {
    list: [],
    updateIcon: null,
  },
  mutations: {
    setList(state, value) {
      state.list = value;
    },
    addNewIcon(state, newIcon) {
      state.list.push(newIcon);
    },
    setUpdateIcon(state, value) {
      state.updateIcon = value;
    },
    updateIcon(state, updateIcon) {
      const updateList = state.list.map((icon) => {
        if (icon._id == updateIcon._id) {
          return updateIcon;
        }
        return icon;
      });
      state.list = updateList;
    },
    deleteIcon(state, delIconId) {
      const updateList = state.list.filter((icon) => icon._id != delIconId);
      state.list = updateList;
    },
  },
  actions: {
    create({ commit }, payload) {
      const { icon, notify } = payload;
      axios.post(url, icon).then((res) => {
        const result = parseInt(res.data.result);
        if (result > 0) {
          const newIcon = res.data.data;
          commit("addNewIcon", newIcon);
          notify({
            title: "Success",
            message: "New Icon is created.",
            type: "success",
          });
        } else {
          notify.error({
            title: "Failed to create icon",
            message: res.data.message,
          });
        }
      });
    },
    loadList({ commit }, payload) {
      const { notify } = payload;
      commit("setGlobalLoading", true, { root: 1 });
      axios
        .get(
          url +
            `/${
              localStorage.getItem("username") == "admin"
                ? "admin"
                : localStorage.getItem("companyId")
            }`
        )
        .then((res) => {
          commit("setGlobalLoading", false, { root: 1 });
          commit("setList", res.data);
        })
        .catch((err) => {
          commit("setGlobalLoading", false, { root: 1 });
          notify.error({
            title: "Failed to load icon",
            message: err.message,
          });
        });
    },
    update({ dispatch }, payload) {
      const { icon, notify, id } = payload;
      axios
        .put(url + "/" + id, icon)
        .then(() => {
          // commit("updateIcon", icon);
          notify({
            title: "Success",
            message: " Icon updated!",
            type: "success",
          });
          dispatch("loadList", { notify: notify });
        })
        .catch((err) => {
          notify.error({
            title: "Failed to update icon",
            message: err.message,
          });
        });
    },
    delete({ commit, state }, payload) {
      const { notify } = payload;
      const deleteIconId = state.updateIcon._id;
      axios
        .delete(url + "/" + deleteIconId)
        .then(() => {
          commit("deleteIcon", deleteIconId);
          notify({
            title: "Success",
            message: "Icon deleted!",
            type: "success",
          });
        })
        .catch((err) => {
          notify.error({
            title: "Failed to delete icon",
            message: err.message,
          });
        });
    },
  },
};
