import { SERVER_URL } from "../../config";
import axios from "axios";
const url = SERVER_URL + "/messages";
export default {
  namespaced: true,
  state: {
    list: [],
    updateMessage: null,
  },
  mutations: {
    setList(state, value) {
      state.list = value;
    },
    addNewMessage(state, newMessage) {
      state.list.push(newMessage);
    },
    setUpdateMessage(state, value) {
      state.updateMessage = value;
    },
    updateMessage(state, updateMessage) {
      const updateList = state.list.map((message) => {
        if (message._id == updateMessage._id) {
          return updateMessage;
        }
        return message;
      });
      state.list = updateList;
    },
    deleteMessage(state, delMessageId) {
      const updateList = state.list.filter(
        (message) => message._id != delMessageId
      );
      state.list = updateList;
    },
  },
  actions: {
    create({ commit }, payload) {
      const { message, notify } = payload;
      axios.post(url, message).then((res) => {
        const result = parseInt(res.data.result);
        if (result > 0) {
          const newMessage = res.data.data;
          commit("addNewMessage", newMessage);
          notify({
            title: "Success",
            message: "New Message is created.",
            type: "success",
          });
        } else {
          notify.error({
            title: "Failed to create message",
            message: res.data.message,
          });
        }
      });
    },
    loadList({ commit }, payload) {
      const { notify } = payload;
      commit("setGlobalLoading", true, { root: 1 });
      axios
        .get(
          url +
            `/${
              localStorage.getItem("username") == "admin"
                ? "admin"
                : localStorage.getItem("companyId")
            }`
        )
        .then((res) => {
          commit("setGlobalLoading", false, { root: 1 });
          commit("setList", res.data);
        })
        .catch((err) => {
          commit("setGlobalLoading", false, { root: 1 });
          notify.error({
            title: "Failed to load message",
            message: err.message,
          });
        });
    },
    update({ commit }, payload) {
      const { message, notify } = payload;
      axios
        .put(url + "/" + message._id, message)
        .then(() => {
          commit("updateMessage", message);
          notify({
            title: "Success",
            message: " Message updated!",
            type: "success",
          });
        })
        .catch((err) => {
          notify.error({
            title: "Failed to update message",
            message: err.message,
          });
        });
    },
    delete({ commit, state }, payload) {
      const { notify } = payload;
      const deleteMessageId = state.updateMessage._id;
      axios
        .delete(url + "/" + deleteMessageId)
        .then(() => {
          commit("deleteMessage", deleteMessageId);
          notify({
            title: "Success",
            message: "Message deleted!",
            type: "success",
          });
        })
        .catch((err) => {
          notify.error({
            title: "Failed to delete message",
            message: err.message,
          });
        });
    },
  },
};
